import { Router } from '@angular/router';
import { async } from '@angular/core/testing';
import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../shared/authentication-service';
import {GlobalService} from '../global.service';
import {Admin} from '../admin';
import { AngularFirestore, AngularFirestoreDocument } from '@angular/fire/firestore';
import { ToastController, ModalController } from '@ionic/angular';
import { ModalClientsPage } from '../modal-clients/modal-clients.page';


@Component({
  selector: 'app-clients',
  templateUrl: './clients.page.html',
  styleUrls: ['./clients.page.scss'],
})
export class ClientsPage implements OnInit {
  
  constructor(private router:Router,public db:AngularFirestore,public authService: AuthenticationService, public global: GlobalService,public toastController: ToastController,public modalCtrl : ModalController)  {
   
   }
   arrayDietists: any = [{
    id: "",
    data: {} as Admin
   }];
   filterTerm: string;
  
  ngOnInit() {

  }
  ionViewWillEnter(){

    if (localStorage.getItem('client')){
      this.router.navigate(['diet']);  
    }
    else{
     var dietistList;
     this.getDietistsList();
    }
   
  }
  

  getDietists() { 
    return this.db.collection("admins").snapshotChanges();
  }

  getDietistsList(){
   this.getDietists().subscribe((result) => {
      this.arrayDietists = [];
      result.forEach((dietistData: any) => {
        this.arrayDietists.push({
          id: dietistData.payload.doc.id,
          data: dietistData.payload.doc.data()
        });
      })
    });

 
  }
  public async openModal(dietistID){
    try {
      
    const modal = await this.modalCtrl.create({
      swipeToClose: true,
      component: ModalClientsPage,
      componentProps: {
        value: dietistID
      }
    });
    modal.onDidDismiss()
      .then((data) => {
        const nameOfUser = data['data']; 
        
    });
    
    return await modal.present();
    
  }
    catch(e){
    
    }
  }
  

}
